import React, {memo} from 'react';

import styled from 'styled-components';

// 미디어쿼리 기능 참조
import mq from '../components/MediaQuery';

// 이미지 참조
import gondol from '../assets/img/gondol.jpg';
import workshop from '../assets/img/workshop.jpg';

const FooterContainer = styled.div`
    /* background-color: #0066ff55; */

    border-top: 1px solid #dddddd;

    .container {
        /* 컨텐츠 영역의 가로 폭 제한 및 정렬 */
        max-width: 1200px;
        margin: auto;
        display: flex;

        /* 모바일 UI에서는 세로 배치 */
        ${mq.maxWidth('md')`
            flex-direction: column;
        `}

        /** footer의 각 영역 비율 설정 */
        .footer-item {
            flex: 1;
            padding: 15px;

            /** 각 영역별 제목 */
            h3 {
                font-size: 24px;
                font-weight: 700px;
                margin: 22px 0;
                text-transform: uppercase;

                ${mq.maxWidth('md')`
                    font-size: 20px;
                    font-weight: 500px;
                    margin: 12px 0;
                `}
            }

            &:nth-child(1) {
                p {
                    font-size: 15px;
                    line-height: 150%;
                    font-weight: 300;
                }
            }

            &:nth-child(2) {
                a {
                    display: flex;
                    margin: 20px 0;
                    padding: 0 10px;

                    img {
                        width: 65px;
                        height: 65px;
                        object-fit: cover;
                        margin-right: 10px;
                    }

                    .blog-post-title {
                        font-weight: bold;
                        margin-bottom: 5px;
                        font-size: 15px;
                    }

                    .blog-post-content {
                        line-height: 1.2;
                    }
                }
            }

            &:nth-child(3) {
                .tags {
                    li {
                        display: inline-block;
                        background-color: #616161;
                        color: #ffffff;
                        padding: 5px 10px;
                        margin-bottom: 8px;
                        margin-right: 5px;
                        font-size: 13px;

                        &.black {
                            background-color: #000000;
                        }
                    }
                }
            }
        }
    }
`;

const Footer = memo(() => {
    const images = [workshop, gondol];
    const tags = [
        "New York", "Dinner", "Salmon", "France", "Drinks", "Ideas", "Flavors", "Cuisine", "Chicken", "Dressing", "Fried", "Fish", "Duck"
    ]
    return (
        <FooterContainer>
            <div className="container">
                {/* 왼쪽 영역 */}
                <div className="footer-item">
                    <h3>FOOTER</h3>
                    <p>
                        Praesent tincidunt sed tellus ut rutrum. Sed vitae justo condimentum, porta lectus vitae, ultricies congue gravida diam non fringilla.
                    </p>
                </div>
                {/* 중앙 영역 */}
                <div className="footer-item">
                    <h3>BLOG POSTS</h3>
                    <ul className="blog-posts">
                        {/* 이미지와 제목, 내용이 있는 블로그 포스트 목록 */}
                        {images.map((v, i) => (
                            <li key={i}>
                                <a href="#">
                                    <img src={v} alt={`Blog post ${i + 1}`} />
                                    <div className="text-box">
                                        <h4 className="blog-post-title">{`Blog post ${i + 1}`}</h4>
                                        <p className="blog-post-content">
                                            Sed mattis nunc Sed mattis nunc Sed mattis nunc
                                        </p>
                                    </div>
                                </a>
                            </li>
                        ))}
                    </ul>
                </div>
                {/* 오른쪽 영역 */}
                <div className="footer-item">
                    <h3>POPULAR TAGS</h3>
                    <ul className="tags">
                        {tags.map((v, i) => {
                            if (i == 0) {
                                return (
                                    <li key={i} className='black'>{v}</li>
                                )
                            } else {
                                return (
                                    <li key={i}>{v}</li>
                                )
                            }
                        })}
                    </ul>
                </div>
            </div>
        </FooterContainer>
    );
});

export default Footer;